<template>
  <div class="star" :class="starType">
    <span v-for="item in classes"
    :key="item.id"
    :class="item"
    class="star-item"></span>
  </div>
</template>

<script type="text/ecmascript-6">
const LENGTH = 5;
const CLS_ON = '_on';
const CLS_HALF = '_half';
const CLS_OFF = '_off';

export default {
  props: {
    size: {
      type: Number,
    },
    score: {
      type: Number,
    },
  },
  computed: {
    starType() {
      return `star-${this.size}`;
    },
    classes() {
      const result = [];
      const score = Math.floor(this.score * 2) / 2;
      const hasDecimal = score % 1 !== 0;
      const integer = Math.floor(score);
      for (let i = 0; i < integer; i += 1) {
        result.push(CLS_ON);
      }
      if (hasDecimal) {
        result.push(CLS_HALF);
      }
      while (result.length < LENGTH) {
        result.push(CLS_OFF);
      }
      return result;
    },
  },
};
</script>

<style lang="stylus">
@import "../../common/stylus/mixin";
  .star
    font-size 0
    .star-item
      display inline-block
      background-repeat no-repeat
      &:last-child
        margin-right 0
    &.star-48
      .star-item
        margin-right 22px
        background-size 20px 20px !important
        width 20px
        height 20px
        &._on
          bg-image('star48_on')
        &._half
          bg-image('star48_half')
        &._off
          bg-image('star48_off')
    &.star-36
      .star-item
        margin-right 5px
        background-size 15px 15px !important
        width 15px
        height 15px
        &:last-child
          margin-right 0
        &._on
          bg-image('star36_on')
        &._half
          bg-image('star36_half')
        &._off
          bg-image('star36_off')
    &.star-24
      .star-item
        margin-right 3px
        background-size 10px 10px !important
        width 10px
        height 10px
        &._on
          bg-image('star24_on')
        &._half
          bg-image('star24_half')
        &._off
          bg-image('star24_off')
</style>



