<template>
  <div class="seller" ref="seller">
    <div class="seller-wrapper">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="desc border-1px">
          <star class="star" :size="36" :score="seller.score"></star>
          <span class="text">（{{seller.ratingCount}}）</span>
          <span class="text">月售{{seller.sellCount}}单</span>
        </div>
        <div class="favor-wrapper" @click="toggleFavorite">
          <i class="icon icon-favorite" :class="{'active':favorite}"></i>
          <p class="text">{{favoriteRext}}</p>
        </div>
        <ul class="remark">
          <li class="block">
            <h2>起送价</h2>
            <p class="content">{{seller.minPrice}}<sub>元</sub></p>
          </li>
          <li class="block">
            <h2>商家配送</h2>
            <p class="content">{{seller.deliveryPrice}}<sub>元</sub></p>
          </li>
          <li class="block">
            <h2>平均配送时间</h2>
            <p class="content">{{seller.deliveryTime}}<sub>分钟</sub></p>
          </li>
        </ul>
      </div>
      <split/>
      <div class="notice">
        <h1 class="title">公告与活动</h1>
        <p class="bulletin border-1px">{{seller.bulletin}}</p>
        <ul class="list" v-if="seller.supports">
          <li class="item border-1px" v-for="(support, index) in seller.supports" :key="index">
            <i class="icon" :class="classMap[support.type]"></i>
            <span class="text">{{support.description}}</span>
          </li>
        </ul>
      </div>
      <split/>
      <div class="pic-wrapper">
        <h1 class="title">商家实景</h1>
        <div class="pics" ref="pics">
          <ul class="pic-list">
            <li class="pic-item" v-for="(pic, index) in seller.pics" :key="index">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split/>
      <div class="info">
        <p class="title border-1px">
          商家信息
        </p>
        <ul class="info-list">
          <li class="item border-1px" v-for="(info, index) in seller.infos" :key="index">
            {{info}}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import split from '@/components/split/split';
import star from '../../components/star/star';
import { saveToLocal, loadFromLocal } from '../../common/js/store';

const BScroll = require('better-scroll');

export default {
  props: {
    seller: {},
  },
  data() {
    return {
      favorite: (() =>
        loadFromLocal(this.seller.id, 'favorite', false))(),
      classMap: ['decrease', 'discount', 'special', 'invoice', 'guarantee'],
    };
  },
  watch: {
    seller: function s() {
      this.initscroll();
    },
  },
  computed: {
    favoriteRext() {
      return this.favorite ? '已收藏' : '收藏';
    },
  },
  created() {
    this.$nextTick(() => {
      // console.log(this.seller);
      this.initscroll();
    });
  },
  methods: {
    initscroll() {
      /* eslint-disable */
      if (!this.scroller) {
        this.scroller = new BScroll(this.$refs.seller, {
          click: true,
        });
      } else {
        this.scroller.refresh();
      }
      if (!this.picScroller) {
        this.picScroller = new BScroll(this.$refs.pics, {
          scrollX: true,
          click: true,
        });
      } else {
        this.picScroller.refresh();
      }
    },
    toggleFavorite() {
      console.log(this.seller);
      this.favorite = !this.favorite;
      saveToLocal(this.seller.id, 'favorite', true);
    },
  },
  components: {
    star,
    split,
  },
};
</script>

<style lang="stylus">
@import '../../common/stylus/mixin';

.seller
  position absolute
  left 0
  top 174px
  bottom 0
  width 100%
  overflow hidden
  .seller-wrapper
    .overview
      padding 18px
      position relative
      .title
        margin-bottom 8px
        line-height 14px
        font-size 14px
        color rgb(7,17,27)
      .favor-wrapper
        position absolute
        width 50px
        right 11px
        top 18px
        text-align center
        .icon
          font-size 24px
          line-height 24px
          color #d4d6d9
          &.active
            color rgb(240,20,20)
        .text
          padding-top 4px
          line-height 10px
          font-size 10px
          color rgb(77,85,93)
      .desc
        padding-bottom  18px
        line-height 18px
        border-1px(rgba(7,17,27,.1))
        font-size 0
        .star
          display inline-block
          vertical-align top
          margin-right 8px
        .text
          display inline-block
          margin-right 12px
          line-height 18px
          font-size 10px
          color rgb(77,85,93)
          vertical-align top
      .remark
        display flex
        padding-top 18px
        .block
          flex 1
          text-align center
          border-right 1px solid rgba(7,17,27,.1)
          &:last-child
            border-rignt none
          h2
            margin-bottom 4px
            line-height 10px
            font-size 10px
            color rgb(147,153,159)
          .content
            line-height 24px
            font-size 24px
            sub
              font-size 10px
    .notice
      width 100%
      padding 0 18px
      .title
        padding-top 18px
        font-size 14px
        line-height 14px
        color rgb(7,17,27)
        font-weight 700
      .bulletin
        padding 8px 12px 18px 12px
        font-size 12px
        line-height 24px
        font-weight 200
        color rgb(240,20,20)
        border-1px(rgba(7,17,27,.1))
      .list
        width 100%
        .item
          padding 16px 12px
          font-size 0
          border-1px(rgba(7,17,27,.1))
          &:last-child
            border-none()
          .icon
            display inline-block
            margin-right 6px
            width 16px
            height 16px
            background-size 16px 16px !important
            background-repeat no-repeat
            vertical-align top
            &.decrease
              bg-image('decrease_4')
            &.discount
              bg-image('discount_4')
            &.guarantee
              bg-image('guarantee_4')
            &.invoice
              bg-image('invoice_4')
            &.special
              bg-image('special_4')
          .text
            line-height 16px
            font-size 12px
            font-weight 200
            color rgb(7,17,27)
    .pic-wrapper
      padding 18px
      .title
        font-size 14px
        line-height 14px
        color rgb(7,17,27)
        font-weight 700
        margin-bottom 12px
      .pics
        width 100%
        overflow hidden
        white-space nowrap
        .pic-list
          font-size 0
          width fit-content
          .pic-item
            display inline-block
            margin-right 6px
            width 120px
            height 90px
            &:last-child
              margin-right 0
    .info
      padding 18px 18px 0 18px
      width 100%
      .title
        padding-bottom 12px
        font-size 14px
        line-height 14px
        color rgb(7,17,27)
        font-weight 700
        border-1px(rgba(7,17,27,.1))
      .info-list
        width 100%
        .item
          padding 16px 12px
          line-height 16px
          font-size 12px
          color rgb(7,17,27)
          font-weight 200
          border-1px(rgba(7,17,27,.1))
          &:last-child
            border-none()
</style>



