<template>
  <transition name="ani">
      <div class="food" v-show="showFlag" ref="foodScroll">
        <div class="food-content">
          <div class="image-header">
            <img :src="nowFood.image">
            <i class="icon-arrow_lift" @click="hide"></i>
          </div>
          <div class="info-wrapper">
            <h1 class="name">{{nowFood.name}}</h1>
            <p class="history">月售{{nowFood.sellCount}}份<span>好评率{{nowFood.rating}}</span> </p>
            <div class="price">￥{{nowFood.price}}
              <span class="old" v-show="nowFood.oldPrice">￥{{nowFood.oldPrice}}</span>
              <div class="control">
                <transition name="fade">
                  <div @click="addFirst($event)"
                  class="add" v-if="nowFood.count==0||!nowFood.count">加入购物车</div>
                </transition>
              </div>
              <div class="control-wrapper" v-if="nowFood.count>0">
                <cart-control :food="nowFood" @cart-add="drop"/>
              </div>
            </div>
          </div>
          <split/>
          <div class="intro">
            <h1 class="title">商品介绍</h1>
            <p class="content">{{nowFood.info}}</p>
          </div>
          <split/>
          <div class="comment">
            <div class="title"></div>
            <rating-select
            :select-type="selectType"
            :only-content="onlyContent"
            :desc="desc"
            :ratings="nowFood.ratings"/>
          </div>
        </div>
      </div>
  </transition>
</template>

<script type="text/ecmascript-6">
import cartControl from '../../components/cartControl/cartControl';
import split from '../../components/split/split';
import ratingSelect from '../../components/ratingSelect/ratingSelect';

const BScroll = require('better-scroll');

// const POSITIVE = 0;
// const NEGATIVE = 1;
const ALL = 2;

export default {
  props: {
    nowFood: {},
  },
  data() {
    return {
      showFlag: false,
      selectType: ALL,
      onlyContent: true,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽',
      },
    };
  },
  methods: {
    show() {
      this.showFlag = true;
      this.selectType = ALL;
      this.onlyContent = true;
      if (!this.scroll) {
        this.$nextTick(() => {
          this.scroll = new BScroll(this.$refs.foodScroll, {
            click: true,
          });
        });
      }
    },
    hide() {
      this.showFlag = false;
    },
    addFirst(event) {
      this.$set(this.nowFood, 'count', 1);
      this.$parent.ballDrop(event.target);
    },
    drop(target) {
      this.$parent.ballDrop(target);
    },
    refresh() {
      this.scroll.refresh();
    },
  },
  created() {
  },
  components: {
    cartControl,
    split,
    ratingSelect,
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.food
  position fixed
  left 0
  top 0
  bottom 48px
  z-index 20
  width 100%
  background #ffffff
  // overflow auto
  &.ani-enter, &.ani-leave-to
    transform translate3d(100%,0,0)
  &.ani-enter-active, &.ani-leave-active
    transition all .2s linear
  &.ani-enter-to, &.ani-leave
    transform translate3d(0,0,0)
  .food-content
    .image-header
      position relative
      width 100%
      height 0
      padding-top 100%
      img
        position absolute
        top 0
        width 100%
        height 100%
      .icon-arrow_lift
        display block
        padding 10px
        width 50px
        height 50px
        position absolute
        left 0
        top 0
        font-size 20px
        color #ffffff
    .info-wrapper
      margin 18px
      position relative
      .name
        font-size 14px
        font-weight 700
        color rgb(7,17,27)
        line-height 14px
        text-align left
      .history
        font-size 10px
        color rgb(147,153,159)
        line-height 10px
        padding-top 8px
        span
          padding-left 12px
      .price
        font-size 14px
        font-weight 700
        color rgb(240,20,20)
        line-height 24px
        margin-top 18px
        position relative
        .old
          font-size 10px
          font-weight 700
          color rgb(147,153,159)
          padding-left 15px
          line-height 24px
          text-decoration line-through
        .control
          position absolute
          right 0
          top 0
          &.fade-enter-active, &.fade-leave-active
            transition opacity .3s
          &.fade-leave, &.fade-enter-to
            opacity 1
          &.fade-leave-to, &.fade-enter
            opacity 0
          .add
            font-size 10px
            color #ffffff
            line-height 24px
            width 74px
            height 24px
            text-align center
            border-radius 12px
            background-color rgb(0,160,220)
        .control-wrapper
          position absolute
          right 0
          top 0
    .intro
      padding 18px
      .title
        font-size 14px
        line-height 14px
      .content
        font-size 12px
        font-weight 200
        color rgb(77,85,93)
        line-height 24px
        padding 6px 8px 0 8px
    .comment
      padding-top 18px
      .title
        font-size 14px
        line-height 14px
        margin-left 18px
</style>



