<template>
  <div class="rating-select">
    <div class="rating-type border-1px">
      <span class="item" @click="select(2)"
      :class="{'active':btnType===2}">
        {{desc.all}}&nbsp;<span class="count">{{ratings.length}}</span></span>
      <span class="item" @click="select(0)"
      :class="{'active':btnType===0}">
        {{desc.positive}}&nbsp;<span class="count">{{positive.length}}</span></span>
      <span class="item" @click="select(1)"
      :class="{'active':btnType===1}">
        {{desc.negative}}&nbsp;<span class="count">{{negative.length}}</span></span>
    </div>
    <div class="switch" @click="toggleContent">
      <i class="icon-check_circle" :class="{'on':olContent}"></i>
      <span class="text">只看有评价的内容</span>
    </div>
    <div class="wrapper" v-show="!sellerType">
      <ul>
        <li class="rating-item border-1px" v-for="(item, index) in ratings" :key="index"
        v-show="needShow(item.rateType, item.text)">
          <div class="user">
            <span class="name">{{item.username}}</span>
            <img :src="item.avatar" class="avatar" width="12" height="12">
          </div>
          <div class="time">{{item.rateTime  | formatDate}}</div>
          <p class="text">
            <span :class="{'icon-thumb_up':item.rateType===0,
            'icon-thumb_down':item.rateType===1}"></span>
            {{item.text}}
          </p>
        </li>
      </ul>
      <div class="no-rating">无更多内容...</div>
    </div>
    <div class="seller-type-wrapper" v-show="sellerType">
      <ul>
        <li class="item border-1px" v-for="(item, index) in ratings" :key="index"
        v-show="needShow(item.rateType, item.text)">
          <div class="avatar">
            <img :src="item.avatar" width="28" height="28">
          </div>
          <div class="content">
            <h1 class="name">{{item.username}}</h1>
            <div class="star-wrapper">
              <star class="star" :size="24" :score="item.score"/>
              <span class="deliveryTime" v-show="item.deliveryTime">
                {{item.deliveryTime}}分钟送达
              </span>
            </div>
            <p class="text">{{item.text}}</p>
            <div class="recommend" v-show="item.recommend&&item.recommend.length!==0">
              <i class="icon-thumb_up"></i>
              <span class="item" v-for="(i, index) in item.recommend" :key="index">
                {{i}}
              </span>
            </div>
            <div class="rate-time">{{item.rateTime | formatDate}}</div>
          </div>
        </li>
      </ul>
      <div class="no-more">已经到底啦...</div>
    </div>
  </div>
</template>

<script>
// import { formatDate } from '../../common/js/date';
import star from '../../components/star/star';

const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;

export default {
  props: {
    ratings: {
      type: Array,
      default() {
        return [];
      },
    },
    selectType: {
      type: Number,
      default: ALL,
    },
    onlyContent: {
      type: Boolean,
      default: false,
    },
    desc: {
      type: Object,
      default() {
        return {
          all: '全部',
          positive: '满意',
          negative: '不满意',
        };
      },
    },
    sellerType: false,
  },
  data() {
    return {
      btnType: false,
      olContent: false,
    };
  },
  computed: {
    positive() {
      return this.ratings.filter(s => s.rateType === POSITIVE);
    },
    negative() {
      return this.ratings.filter(s => s.rateType === NEGATIVE);
    },
  },
  methods: {
    select(type) {
      this.btnType = type;
      this.$parent.selectType = type; // 修改父组件默认配置
      this.$nextTick(() => {
        this.$parent.refresh();
      });
    },
    toggleContent() {
      this.olContent = !this.olContent;
      this.$parent.onlyContent = this.olContent;
      this.$nextTick(() => {
        this.$parent.refresh();
      });
    },
    needShow(type, text) {
      if (this.olContent && !text) {
        return false;
      }
      if (this.btnType === ALL) {
        return true;
      }
      return type === this.btnType;
    },
  },
  components: {
    star,
  },
  created() {
    this.btnType = this.selectType;
    this.olContent = this.onlyContent;
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
.rating-select
  .rating-type
    padding 18px 0
    width 100%
    margin 0 18px
    border-1px(rgba(7,17,27,.5))
    font-size 0
    .item
      display inline-block
      padding 8px 12px
      font-size 12px
      color rgb(77,85,93)
      line-height 16px
      background-color rgba(0,160,220,.2)
      margin-right 8px
      border-radius 2px
      &.active
        background-color rgba(0,160,220,1)
        color #fff
      .count
        font-size 8px
        line-height 100%
  .switch
    padding 12px 18px
    border-bottom 1px solid rgb(147,153,159)
    color rgb(147,153,159)
    line-height 24px
    font-size 0
    .icon-check_circle
      display inline-block
      font-size 24px
      margin-right 4px
      vertical-align top
      &.on
        color green
    .text
      font-size 12px
  .wrapper
    padding 0 18px
    position relative
    .rating-item
      position relative
      padding 16px 0
      border-1px(rgba(7,17,27,.2))
      .user
        position absolute
        right 18px
        top 16px
        font-size 0
        line-height 12px
        .name
          display inline-block
          font-size 10px
          color rgb(147,153,159)
          margin-right 6px
        .avatar
          border-radius 50%
      .time
        margin-bottom 6px
        line-height 12px
        font-size 10px
        color rgb(147,153,159)
      .text
        line-height 16px
        font-size 12px
        color rgb(7,17,27)
        .icon-thumb_up, .icon-thumb_down
          line-height 16px
          margin-right 4px
          font-size 12px
        .icon-thumb_up
          color rgb(0,160,220)
    .no-rating
      width 100%
      font-size 12px
      text-align center
      line-height 12px
      padding 18px
  .seller-type-wrapper
    padding 0 18px
    .item
      display flex
      padding 18px 0
      border-1px(rgba(7,17,27,.1))
      .avatar
        flex 0 0 28px
        margin-right 12px
        img
          border-radius 50%
      .content
        flex 1
        position relative
        .name
          line-height 12px
          font-size 12px
          color rgb(7,17,27)
        .star-wrapper
          padding-top 4px
          .star
            display inline-block
          .deliveryTime
            line-height 12px
            font-size 10px
            color rgb(147,153,159)
            font-weight 200
            display inline-block
        .text
          padding 6px 0 8px 0
          line-height 18px
          font-size 12px
          color rgb(7,17,27)
        .recommend
          font-size 0
          .icon-thumb_up
            display inline-block
            font-size 12px
            line-height 16px
            color rgb(0,160,220)
            margin-right 8px
          .item
            display inline-block
            margin-right 8px
            margin-bottom 4px
            padding 0 6px
            font-size 9px
            line-height 16px
            border 1px solid rgba(7,17,27,.1)
            background-color #ffffff
        .rate-time
          position absolute
          top 0
          right 18px
          line-height 12px
          font-size 10px
          font-weight 200
          color rgb(147,153,159)
    .no-more
      text-align center
      font-size 10px
      line-height 10px
      padding 18px
      color rgb(147,153,159)
</style>




