<template>
  <div class="ratings" ref="rating">
    <div class="rating-content">
      <div class="overview">
        <div class="overview-left">
          <p class="score">{{seller.score}}</p>
          <p class="title">综合评分</p>
          <p class="rank">高于周围商家{{seller.rankRate}}%</p>
        </div>
        <div class="overview-right">
          <div class="row">
            <span class="title">服务态度</span>
            <star :size="36" :score="seller.serviceScore"/>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="row">
            <span class="title">商品评分</span>
            <star class="star" :size="36" :score="seller.foodScore"/>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="row">
            <span class="title">送达时间</span>
            <span class="time">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split/>
      <rating-select :selectType="selectType"
      :onlyContent="onlyContent"
      :desc="desc"
      :ratings="ratings"
      :sellerType="true"/>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import star from '../../components/star/star';
import split from '../../components/split/split';
import ratingSelect from '../../components/ratingSelect/ratingSelect';

const BScroll = require('better-scroll');

const ALL = 2;

export default {
  props: {
    seller: {},
  },
  data() {
    return {
      ratings: [],
      selectType: ALL,
      onlyContent: true,
      desc: {
        all: '全部',
        positive: '满意',
        negative: '不满意',
      },
    };
  },
  methods: {
    refresh() {
      this.scroll.refresh();
    },
  },
  created() {
    this.$http.get('/rating').then((res) => {
      const data = res.body;
      if (data.errno === 0) {
        this.ratings = data.data;
      }
      if (!this.scroll) {
        this.$nextTick(() => {
          this.scroll = new BScroll(this.$refs.rating, {
            click: true,
          });
        });
      }
    });
  },
  components: {
    star,
    split,
    ratingSelect,
  },
};
</script>

<style lang="stylus">
.ratings
  position absolute
  left 0
  top 174px
  bottom 0
  width 100%
  overflow hidden
  .rating-content
    .overview
      display flex
      padding 18px 0
      .overview-left
        flex 0 0 137px
        padding 6px 0
        width 137px
        border-right 1px solid rgba(7,17,27,.1)
        text-align center
        @media only screen and (max-width 320px)
          flex 0 0 120px
          width 120px
        .score
          line-height 28px
          font-size 24px
          color rgb(255,153,0)
          margin-bottom 6px
        .title
          margin-bottom 8px
          line-height 12px
          font-size 12px
          color rgb(7,17,27)
        .rank
          line-height 10px
          font-size 10px
          color rgb(147,153,159)
      .overview-right
        flex 1
        padding 6px 0 6px 24px
        @media only screen and (max-width 320px)
          padding-left 6px
        .row
          margin-bottom 8px
          font-size 0
          .title
            line-height 18px
            display inline-block
            vertical-align top
            font-size 12px
            color rgb(7,17,27)
          .star
            display inline-block
            vertical-align top
            margin 0 12px
          .score
            display inline-block
            vertical-align top
            font-size 12px
            line-height 18px
            color rgb(255,153,0)
          .time
            margin-left 12px
            font-size 12px
            line-height 18px
            color rgb(147,153,159)
</style>



