<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar" alt="11">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div class="support" v-if="seller.supports">
          <span class="icon_" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div @click="showDetail" v-if="seller.supports" class="support-count">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper"  @click="showDetail">
      <span class="title"></span><span class="text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar">
    </div>
    <transition name="fade">
      <div class="detail" v-show="detailShow" transition="fade">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <div class="detail-head">
              <div class="title">{{seller.name}}</div>
              <div class="star-wrapper">
                <star :size="48" :score="seller.score"/>
              </div>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li class="item" v-for="(item, index) in seller.supports" :key="item.type">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">
                  {{seller.supports[index].description}}
                </span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close" @click="doClose">
          <i class="icon-close"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
import star from '../../components/star/star';

export default {
  props: {
    seller: {
      type: Object,
    },
  },
  data() {
    return {
      classMap: '',
      detailShow: false,
    };
  },
  methods: {
    showDetail: function show() {
      this.detailShow = true;
    },
    doClose: function doclose() {
      this.detailShow = false;
    },
  },
  created: function create() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
  components: {
    star,
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
  .header
    position relative
    color #ffffff
    .content-wrapper
      position relative
      padding 24px 12px 18px 24px
      text-align left
      font-size 0
      .avatar
        display inline-block
        width 64px
        vertical-align top
        img
          border-radius 2px
      .content
        display  inline-block
        font-size 14px
        margin-left 16px
        .title
          margin 2px 0 8px 0
          .brand
            display inline-block
            width 30px
            height 18px
            bg-image("brand")
            background-size 30px 18px
            background-repeat no-repeat
            vertical-align top
          .name
            margin-left 6px
            font-size 16px
            line-height 18px
            font-weight bold
        .description
          margin-bottom 10px
          line-height 12px
          font-size 12px
        .support
          .icon_
            display inline-block
            vertical-align top
            width 12px
            height 12px
            margin-right 4px
            background-size 12px 12px !important
            background-repeat no-repeat
            &.decrease
              bg-image('decrease_1')
            &.discount
              bg-image('discount_1')
            &.guarantee
              bg-image('guarantee_1')
            &.invoice
              bg-image('invoice_1')
            &.special
              bg-image('special_1')
          .text
            font-size 10px
            line-height 12px

      .support-count
        position absolute
        right 12px
        bottom 18px
        padding 0 8px
        height 24px
        line-height 24px
        border-radius 12px
        background-color rgba(0,0,0,.2)
        text-align center
        cursor pointer
        .count
          font-size 10px
        .icon-keyboard_arrow_right
          font-size 10px
          margin-left 5px

    .bulletin-wrapper
      position relative
      height 28px
      line-height 28px
      white-space nowrap
      overflow hidden
      text-overflow ellipsis
      background rgba(7,1,7,.5)
      .title
        display inline-block
        vertical-align top
        margin-top 10px
        margin-left 12px
        height 12px
        width 22px
        bg-image('bulletin')
        background-size 22px 12px
        background-repeat no-repeat
      .text
        margin 0 0 0 4px
        font-size 10px
      .icon-keyboard_arrow_right
        position absolute
        right 0
        bottom 5px
        font-size 10px
    .background
      position absolute
      top 0
      left 0
      width 100%
      height 100%
      z-index -1
      filter blur(10px)
      background rgba(7,17,27,.8)
      img
        width 100%
        height 100%
    .detail
      position fixed
      top 0px
      z-index 100
      width 100%
      height 100%
      overflow auto
      background-color rgba(7,17,27,.8)
      backdroop-filter 10px
      transition .5s
      .detail-wrapper
        min-height 100%
        width 100%
        .detail-main
          margin-top 64px
          padding-bottom 64px
          text-align center
          .detail-head
            width 100%
            margin-bottom 28px
            text-align center
            .title
              display inline-block
              font-size 16px
              font-weight 700
              color rgb(255,255,255)
              line-height 16px
              text-align center
            .star-wrapper
              width 100%
              margin-top 18px
              padding 2px 0
              text-align center
          .title
            display flex
            width 80%
            margin 28px auto 24px auto
            .line
              flex 1
              position relative
              top -6px
              border-bottom 1px solid rgba(255,255,255,.2)
            .text
              padding 0 12px
              font-size 14px
              font-weight 700
              text-align center
              width fit-content
          .supports
            width 80%
            margin 0 auto
            width 75%
            .item
              padding 0 12px
              margin-bottom 12px
              font-size 0
              text-align left
              &:last-child
                margin-bottom 0
              .icon
                display inline-block
                width 16px
                height 16px
                vertical-align top
                margin-right 6px
                background-size 16px 16px !important
                &.decrease
                  bg-image('decrease_2')
                &.discount
                  bg-image('discount_2')
                &.guarantee
                  bg-image('guarantee_2')
                &.invoice
                  bg-image('invoice_2')
                &.special
                  bg-image('special_2')
              .text
                line-height 16px
                font-size 12px !important
        .bulletin
          width 80%
          margin 0 auto
          .content
            padding 0 12px
            line-height 24px
            font-size 12px
      .detail-close
        position relative
        width 32px
        height 32px
        margin -64px auto 0 auto
        text-align center
        clear both
        font-size 32px
    .fade-enter-active, .fade-leave-active
      transition opacity .5s
    .fade-enter, .fade-leave-to
      opacity 0
</style>



