<template>
  <div id="app">
    <vheader :seller="seller"></vheader>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link class="link" to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link class="link" to="/ratings">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link class="link" to="/seller">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"/>
    </keep-alive>
  </div>
</template>

<script>
import header from '@/components/header/header';
import { urlParse } from './common/js/util';

export default {
  name: 'App',
  data() {
    return {
      seller: {
        id: (() => {
          const queryParam = urlParse();
          // console.log(queryParam);
          return queryParam.id;
        })(),
      },
    };
  },
  created: function create() {
    this.$http.get(`/seller?id=${this.seller.id}`).then((response) => {
      const res = response.body;
      // console.log(this.seller);
      if (res.errno === 0) {
        this.seller = Object.assign({}, this.seller, res.data);
      }
    });
  },
  components: {
    vheader: header,
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "./common/stylus/mixin.styl";
@import "./common/stylus/index.styl"
#app
  width 100%
  color: #2c3e50
  .tab
    display: flex
    width: 100%
    height: 40px
    line-height: 40px
    border-1px(rgba(7,17,22,.5))
    .tab-item
      flex: 1
      text-align center
      .link
        display block
        font-size 14px
        color rgb(77,85,93)
        line-height 40px
        &.active
          color rgb(240,20,20)
</style>



