<template>
  <div class="wrap">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li @click="selectMenu(index)"
        class="menu-item"
        v-for="(item, index) in goods" :key="index"
        :class="{current: currentIndex === index}">
          <span class="text border-1px">
            <span class="icon" v-show="item.type>0"
              :class="classMap[item.type]">
            </span>
            {{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodWrapper">
      <ul>
        <li v-for="(item, index) in goods" :key="index"
        class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="(food, n) in item.foods" :key="n"
            class="food-item border-1px" @click.stop.prevent="selectFood(food,$event)">
              <div class="icon">
                <img :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}</span>
                  <span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span>
                  <span class="old" v-show="food.oudPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartControl-wrapper">
                  <cart-control
                  :food="food"
                  @cart-add="ballDrop"></cart-control>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart :dPrice="seller.deliveryPrice"
    :minPrice="seller.minPrice"
    :selected="selectedFood"
    ref="shopcart"/>
    <food :nowFood="nowFood" ref="food"/>
  </div>
</template>

<script>
// import Vue from 'vue';
import shopcart from '../../components/shopcart/shopcart';
import cartControl from '../../components/cartControl/cartControl';
import food from '../../components/food/food';

const BScroll = require('better-scroll');

export default {
  props: {
    seller: {
      type: Object,
    },
  },
  components: {
    shopcart,
    cartControl,
    food,
  },
  data() {
    return {
      goods: [],
      classMap: [],
      listHeight: [],
      scrollY: 0,
      nowFood: Object,
    };
  },
  created: function c() {
    this.$http.get('/goods').then((res) => {
      const data = res.body;
      if (data.errno === 0) {
        this.goods = data.data;
        this.$nextTick(() => {
          this.initScroll();
          this.calculateHeight();
        });
      }
    });
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },
  methods: {
    initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {
        click: true,
      });
      this.foodScroll = new BScroll(this.$refs.foodWrapper, {
        probeType: 3,
        click: true,
      });
      this.foodScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    calculateHeight() {
      const foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook');
      let height = 0;
      this.listHeight.push(height);
      for (let i = 0; i < foodList.length; i += 1) {
        const item = foodList[i];
        height += item.clientHeight;
        this.listHeight.push(height);
      }
    },
    selectMenu(index) {
      const foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook');
      const ele = foodList[index];
      this.foodScroll.scrollToElement(ele, 500);
    },
    ballDrop(target) {
      this.$nextTick(() => { // 性能优化，异步执行下落动画  让他等到下一个周期在执行
        this.$refs.shopcart.drop(target);
        // console.log(target);
      });
    },
    selectFood(nowfood, event) {
      // console.log(event);
      /* eslint-disable */
      if (!event._constructed) {
        return;
      }
      this.nowFood = nowfood;
      this.$refs.food.show();
    },
  },
  computed: {
    currentIndex() {
      for (let i = 0; i < this.listHeight.length; i += 1) {
        const height1 = this.listHeight[i];
        const height2 = this.listHeight[i + 1];
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          return i;
        }
      }
      return 0;
    },
    selectedFood() {
      const foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count > 0) {
            foods.push(food);
          }
        });
      });
      return foods;
    },
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
.wrap
  position absolute
  top 175px
  bottom 46px
  display flex
  width 100%
  overflow hidden
  .menu-wrapper
    flex 0 0 80px
    width 80px
    background #f3f5f7
    .menu-item
      display table
      width 56px
      height 54px
      line-height 14px
      padding 0 12px
      &.current
        position relative
        margin-top -1px
        font-weight 700
        background-color #ffffff
        z-index 5
        .text
          border-none()
      .icon
        display inline-block
        vertical-align top
        width 12px
        height 12px
        margin-right 2px
        background-size 12px 12px !important
        background-repeat no-repeat
        &.decrease
          bg-image('decrease_3')
        &.discount
          bg-image('discount_3')
        &.guarantee
          bg-image('guarantee_3')
        &.invoice
          bg-image('invoice_3')
        &.special
          bg-image('special_3')

      .text
        display table-cell
        width 56px
        vertical-align middle
        font-size 12px
        border-1px(rgba(7,17,27,.1))
  .foods-wrapper
    flex 1
    .title
      padding-left 14px
      height 26px
      line-height 26px
      border-left 2px solid #d9dde1
      font-size 12px
      color reb(147,153,159)
      background #f3f5f
    .food-item
      display flex
      padding 18px
      border-1px(rgba(7,17,27,.1))
      &:last-child
        border-none()
        margin-bottom 0
      .icon
        flex 0 0 57px
        width 57px
        height 57px
        margin-right 10px
        img
          width 57px
          height 57px
      .content
        flex 1
        .name
          margin 2px 0 8px 0
          height 14px
          line-height 14px
          font-size 14px
          color rgb(7,17,27)
        .desc
          margin-bottom 8px
          font-size 10px
          color rgb(147,153,159)
          line-height 12px
        .extra
          font-size 10px
          color rgb(147,153,159)
          line-height 10px
          .count
            margin-right 12px
        .price
          font-weight 700
          line-height 24px
          .now
            margin-right 8px
            font-size 14px
            color rgb(240,20,20)
          .old
            text-decoration line-through
            font-size 10px
            color rgb(147,153,159)
        .cartControl-wrapper
          position absolute
          right 0
          bottom 12px
</style>



