<template>
  <div class="cartControl">
    <transition name="ddd">
        <div class="decrease icon-remove_circle_outline"
        v-show="food.count>0" @click.stop="decCart">
        </div>
    </transition>
    <transition name="fade">
      <div class="count" v-if="food.count>0">{{food.count}}</div>
    </transition>
    <div class="add icon-add_circle"
    @click.stop="addCart"></div>
  </div>
</template>

<script type="text/ecmascript-6">
import Vue from 'vue';

/* eslint-disable */
export default {
  props: {
    food: {},
  },
  methods: {
    addCart(event) {/*
      if (!event._constructed) {
        return;
      }*/
      if (!this.food.count) {
        Vue.set(this.food, 'count', 1);
      } else {
        this.food.count += 1;
      }
      this.$emit('cart-add', event.target);
    },
    decCart() {/*
      if (!event._constructed) {
        return;
      }*/
      if (this.food.count <= 0) {
        return;
      }
      this.food.count -= 1;
    }
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.cartControl
  z-index 200000
  font-size 0
  text-align center
  .decrease, .add
    display inline-block
    padding 6px
    font-size 24px
    line-height 24px
    color rgb(0,160,220)
    &.ddd-enter, &.ddd-leave-to
      transform translate3D(100px, 0, 0) rotate(360deg)
      opacity 0
    &.ddd-enter-active, &.ddd-leave-active
      transition all .5s
    &.ddd-enter-to
      transform translate3D(0) rotate(0)
      opacity 1
    &.ddd-leave
      transform translate3D(0) rotate(0)
      opacity 1
  .count
    display inline-block
    font-size 10px
    line-height 24px
    padding 6px
    width 12px
    text-align center
    vertical-align top
    color rgb(147,153,156)
    &.fade-enter, &.fade-leave-to
      opacity 0
    &.fade-enter-active, &.fade-leave-active
      transition all .5s
</style>



