<template>
  <div class="shopcart">
    <div class="content" @click="toggleList">
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo" :class="{'highlight': totalCount>0}">
            <i class="icon-shopping_cart" :class="{'highlight': totalCount>0}"></i>
          </div>
          <div class="bage" v-show="totalCount>0">
            {{totalCount}}
          </div>
        </div>
        <div class="price" :class="{'highlight': totalCount>0}">￥{{totalPrice}}</div>
        <div class="desc">另需配送费￥{{dPrice}}元</div>
      </div>
      <div class="content-right">
        <div class="pay" v-if="totalCount===0">
            ￥{{minPrice}}元起送
        </div>
        <div class="wait" v-if="totalPrice<minPrice">还差￥{{minPrice-totalPrice}}起送</div>
        <div class="gopay" @click.stop.prevent="pay"
        v-if="totalPrice>=minPrice">去结算￥{{totalPrice}}</div>
      </div>
    </div>
    <div class="ball-container">
      <transition name="drop" v-for="(ball, index) in balls" :key="index"
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
      v-on:after-enter="afterEnter">
        <div class="ball" v-show="ball.show">
          <div class="inner"></div>
        </div>
      </transition>
    </div>
    <transition name="fold"
    >
      <div class="shopcart-list" v-show="!fold">
        <div class="list-header">
          <h1 class="title">购物车</h1>
          <span class="empty" @click="clearCart">清空</span>
        </div>
        <div class="list-content" ref="listContent">
          <ul>
            <li class="food" v-for="(food, index) in selected" :key="index">
              <span class="name">{{food.name}}</span>
              <div class="price">
                <span>￥{{food.count*food.price}}</span>
              </div>
              <div class="cartcontrol-wrapper">
                <cart-control :food="food"/>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </transition>
    <transition name="back">
      <div class="back" v-if="!fold"
      @click="fold = true"></div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
import cartControl from '../../components/cartControl/cartControl';

const BScroll = require('better-scroll');

export default {
  props: {
    dPrice: Number,
    minPrice: Number,
    selected: {
      type: Array,
      default() {
        return [
          {
            price: 0,
            count: 0,
          },
        ];
      },
    },
  },
  components: {
    cartControl,
  },
  data() {
    return {
      balls: [
        {
          show: false,
        },
        {
          show: false,
        },
        {
          show: false,
        },
        {
          show: false,
        },
        {
          show: false,
        },
      ],
      dropBalls: [],
      fold: true,
    };
  },
  watch: {
    /* eslint-disable */
    totalCount: function () {
      if (!this.totalCount) {
        this.fold = true;
        return false;
      }
    },
    fold: function () {
      const show = !this.fold;
      if (show) {
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.listContent, {
              click: true,
            });
          } else {
            this.scroll.refresh();
          }
        });
      }
      return show;
    },
  },
  computed: {
    totalPrice() {
      let total = 0;
      this.selected.forEach((food) => {
        total += food.price * food.count;
      });
      return total;
    },
    totalCount() {
      let count = 0;
      this.selected.forEach((food) => {
        count += food.count;
      });
      return count;
    },
  },
  methods: {
    /* eslint-disable*/
    clearCart() {
      this.selected.forEach((food) => {
        food.count = 0;
      });
    },
    pay() {
      if (this.totalPrice < this.minPrice) {
        return;
      }
      window.alert('可以支付了！！');
    },
    drop(el) {
      for (let i = 0; i < this.balls.length; i += 1) {
        let ball = this.balls[i];
        if (!ball.show) {
          ball.show = true;
          ball.el = el;
          this.dropBalls.push(ball);
          return;
        }
      }
    },
    beforeEnter: function beforeEnter(el) {
      /* eslint-disable*/
      let count = this.balls.length -1; // 重大错误 count是5，但是balls索引是0-4，所以要-1
      while (count >= 0) {
        const ball = this.balls[count];
        if (ball.show) {
          const rect = ball.el.getBoundingClientRect();
          const x = rect.left - 32;
          const y = -window.innerHeight + rect.top + 40;
          el.style.display = 'block';
          el.style.webkitTransform = `translateY(0,${y}px,0)`;
          el.style.transform = `translateY(${y}px)`;
          const inner = el.getElementsByClassName('inner')[0];
          inner.style.webkitTransform = `translateX(${x}px)`;
          inner.style.transform = `translateX(${x}px)`;
        }
        count -= 1;
      }
    },
    enter: function enter(el) {
      /* eslint-disable no-unused-vars */
      let rf = el.offsetHeight; // 触发视窗更新  本身并没有其他意义
      this.$nextTick(() => {
        el.style.webkitTransform = 'translateY(0)';
        el.style.transform = 'translateY(0)';
        let inner = el.getElementsByClassName('inner')[0];
        inner.style.webkitTransform = `translateX(0)`;
        inner.style.transform = `translateX(0)`;
      });
    },
    afterEnter: function afterEnter(el) {
      let ball = this.dropBalls.shift(); // shift() 删除数组首个元素并返回 
      if (ball) {
        ball.show = false;
        el.style.display = 'none';
      }
    },
    toggleList() {
      if (!this.totalCount) {
        return;
      }
      this.fold = !this.fold;
    },
  },
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.shopcart
  position fixed
  bottom 0
  width 100%
  height 48px
  background-color #000
  z-index 50
  .content
    display flex
    bakcground #141d27
    font-size 0
    z-index 55
    .content-left
      flex 1
      .logo-wrapper
        display inline-block
        position relative
        top -10px
        margin 0 12px
        padding 6px
        width 56px
        height 56px
        box-sizing border-box
        vertical-align top
        border-radius 50%
        background #141d27
        .logo
          width 100%
          height 100%
          border-radius 50%
          background #2b343c
          text-align center
          &.highlight
            background-color rgb(0,160,220)
          .icon-shopping_cart
            font-size 24px
            color #80858a
            line-height 44px
            &.highlight
              color #ffffff
        .bage
          position absolute
          top 0px
          right -3px
          width 24px
          height 16px
          line-height 16px
          text-align center
          border-radius 16px
          font-size 9px
          font-weight 700
          color #ffffff
          background-color rgb(240,20,20)
          box-shadow 0 4px 8px 0 rgba(0,0,0,.4)
      .price
        display inline-block
        vertical-align top
        line-height 24px
        margin-top 12px
        box-sizing border-box
        padding-right 12px
        border-right 1px solid rgba(255,255,255,.1)
        font-size 16px
        font-weight 700
        color rgba(255,255,255,.4)
        &.highlight
          color #ffffff
      .desc
        display inline-block
        vertical-align top
        line-height 24px
        margin-left 12px
        margin-top 12px
        font-size 12px
        font-weight 600
        color rgba(255,255,255,.4)
    .content-right
      flex 0 0 105px
      width 105px
      .pay, .wait, .gopay
        font-size 12px
        height 48px
        line-height 48px
        text-align center
        color rgba(255,255,255,.5)
      .pay
        background-color #2b333b
      .wait
        color rgba(255,255,255,.5)
        font-weight 700
      .gopay
        background-color green
        font-weight 700
  .ball-container
    .ball
      position absolute
      width 16px
      height 16px
      left 32px
      bottom 22px
      z-index 2000
      &.drop-enter-active, &.drop-leave-active
        transition all .5s cubic-bezier(.91,-0.14,1,.5)
      .inner
        width 16px
        height 16px
        border-radius 50%
        background rgb(0,160,220)
        transition all .5s linear
  .shopcart-list
    position absolute
    top 0
    z-index -1
    transform translateY(-100%)
    width 100%
    background yellow
    transition .5s
    &.fold-enter, &.fold-leave-to
      transform translateY(50px)
    &.fold-enter-to, &.fold-leave
      transform translateY(-100%)
    &.fold-enter-active, &.fold-leave-active
      transition .5s
    .list-header
      z-index -1
      height 40px
      line-height 40px
      padding 0 18px
      background #f3f5f7
      border-bottom 1px solid rgba(7,17,27,.1)
      .title
        z-index -1
        float left
        font-size 14px
        color rgb(7,17,27)
      .empty
        z-index -1
        float right
        font-size 12px
        color rgb(0,160,220)
    .list-content
      z-index -1
      padding 0 18px
      max-height 217px
      background #ffffff
      overflow hidden
      .food
        position relative
        height 48px
        padding 12px 0
        box-sizing border-box
        border 1px solid rgba(7,17,27,.1)
        .name
          line-height 24px
          font-size 14px
          color rgb(7,17,27)
        .price
          position absolute
          right 90px
          bottom 12px
          padding-right 12px 
          line-height 24px
          font-size 14px
          font-weight 700
          color rgb(240,20,20)
        .cartcontrol-wrapper
          position absolute
          width fit-content
          right 0
          bottom 6px
  .back
    width 100%
    height 3000px
    position absolute
    top -1500px
    opacity 1
    z-index -2
    -webkit-backdrop-filter blur(10px)
    background rgba(7,17,27,.5)
  .back-enter, .back-leave-to
    opacity 0
  .back-enter-active, .back-leave-active
    transition all 1s
  .back-enter-to, .back-leave
    opacity 1
</style>



